import React from "react";
import './style.css'

const UserList = (props) => {

    // hash function taken from here https://gist.github.com/hyamamoto/fd435505d29ebfa3d9716fd2be8d42f0
    function hashCode(s) {
        for(var i = 0, h = 0; i < s.length; i++)
            h = Math.imul(31, h) + s.charCodeAt(i) | 0;
        return Math.abs(h);
    }

    const orderedUsers = Array.from(props.activeUsers).sort()
    const usersList = orderedUsers.map((user, id) => {
        return (
            <div key={id}>
                <img src={`https://tigermli.github.io/chatapp_frontend_index/profiles/doge${hashCode(user)%9+1}.jpg`} width="30" height="30"></img>
                <div className='userItem' key={id}>{user}</div>
            </div>
        )
    });

    return (
        <div className="userListContainer" >
            <div className="userList">
                {usersList}
            </div>
        </div>
    )
}

export default UserList
