// import logo from './logo.svg';
import './App.css'
import './components/Chat/style.css';
import './components/SendUserMessage/style.css';
import './components/UserList/style.css';
import React, {useEffect, useState} from "react";
import LoginModal from './components/LoginModal/index.js'
import Chat from './components/Chat/index.js';

const App = () => {
    const [disableChat, setDisableChat] = useState(true)

    // check for duplicate case
    useEffect(()=>{
        if (sessionStorage.getItem('token') !== null) {
            setDisableChat(false);
        }
    }, []);

    // wipe session storage on reload
    window.addEventListener('unload', function(event) {
        sessionStorage.clear();
    });

    function childSetDisableChat(value) {
        setDisableChat(value)
    }

    return (
        <div>
            <LoginModal
                disableChat={disableChat}
                onSetDisableChat={childSetDisableChat} />
            <Chat
                disableChat={disableChat}
                onSetDisableChat={childSetDisableChat} />
        </div>
    )
}

export default App;
