import React, {useEffect, useState} from "react";
import './style.css'
import '../SendUserMessage/style.css';
import '../UserList/style.css';
import MessageList from '../MessageList/index.js';
import SendUserMessage from '../SendUserMessage/index.js';
import UserList from '../UserList/index.js';

const Chat = (props) => {
    const [users, setUsers] = useState(new Set())
    const [messages, setMessages] = useState([])
    
    function onEventArrival(event) {
        setMessages(arr => [...arr, event]);
    };

    function addToUsers(event) {
        setUsers((set) => {
            let newSet = new Set(set);
            newSet.add(event);
            return newSet;
        });
    };

    function removeFromUsers(event) {
        setUsers((set) => {
            let oldSet = new Set(set);
            oldSet.delete(event);
            return oldSet;
        });
    };

    function formatTime(timeCreated) {
        var t = new Date(timeCreated * 1000);
        var formatted = t.toLocaleDateString("en-US") + " " + t.toLocaleTimeString("en-US")
        return formatted
    };

    useEffect(() => {
        if (!props.disableChat) {
            let stream = new EventSource(
                sessionStorage.getItem('url') + "/stream/" + sessionStorage.getItem('token')
            );

            // done
            stream.addEventListener(
                "Users",
                function(event) {
                    setUsers(new Set(JSON.parse(event.data).users));                    
                },
                false
            );

            stream.addEventListener(
                "Message",
                function(event) {
                    let payload = JSON.parse(event.data)
                    const formattedTime = formatTime(payload.created)
                    onEventArrival({
                        "str": formattedTime + " (" + payload.user + ") " + payload.message,
                        "user": payload.user,
                        "isMessage": true
                    })
                },
                false
            );

            // done
            stream.addEventListener(
                "Disconnect",
                function(event) {
                    stream.close()
                    props.onSetDisableChat(true)
                },
                false
            );

            // done
            stream.addEventListener(
                "ServerStatus",
                function(event) {
                    let payload = JSON.parse(event.data)
                    const formattedTime = formatTime(payload.created)
                    
                    onEventArrival({
                        "str": formattedTime + " STATUS: " + payload.status,
                        "isMessage": false
                    })
                },
                false
            );

            // done
            stream.addEventListener(
                "Part",
                function(event) {
                    let payload = JSON.parse(event.data);

                    const formattedTime = formatTime(payload.created);
                    onEventArrival({
                        "str": formattedTime + " PART: " + payload.user,
                        "isMessage": true,
                        "user": payload.user
                    })
                    removeFromUsers(payload.user);                    
                },
                false
            );

            // done
            stream.addEventListener(
                "Join",
                function(event) {
                    let payload = JSON.parse(event.data);

                    const formattedTime = formatTime(payload.created);
                    onEventArrival({
                        "str": formattedTime + " JOIN: " + payload.user,
                        "isMessage": true,
                        "user": payload.user
                    })
                    addToUsers(payload.user);
                },
                false
            );
        }

        console.log('visited effect')
    }, [props.disableChat]);


    if (!props.disableChat) {
        return (
            <div className = "chat">
                <div className="leftContainer">
                    <MessageList messages={[...messages]}/>
                    <SendUserMessage
                        url={sessionStorage.getItem("url")}
                        disabled={props.disableChat} />
                </div>
                <UserList activeUsers={users}/>
            </div>
        )
    } else {
        return null
    }
}

export default Chat
