import React, {useEffect, useRef} from "react";
import './style.css'

const MessageList = (props) => {

    // hash function taken from here https://gist.github.com/hyamamoto/fd435505d29ebfa3d9716fd2be8d42f0
    function hashCode(s) {
        for(var i = 0, h = 0; i < s.length; i++)
            h = Math.imul(31, h) + s.charCodeAt(i) | 0;
        return Math.abs(h);
    }

    const entireMessageList = props.messages.map((message, id) => {
        return (
            <div key={id}>
                {message['isMessage'] && <img src={`https://tigermli.github.io/chatapp_frontend_index/profiles/doge${hashCode(message['user'])%9+1}.jpg`} width="30" height="30"></img>}
                {!message['isMessage'] && <img src={'https://tigermli.github.io/chatapp_frontend_index/profiles/noDoge.png'} width="30" height="30"></img>}
                {message['str']}
            </div>
        )
    });

    // scroll to bottom automatically
    const messagesEndRef = useRef(null)
    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
    }
    useEffect(() => {
        scrollToBottom()
    }, [props]);

    return (
        <div className="messageListContainer">
            {entireMessageList}
            <div ref={messagesEndRef} />
        </div>
    )
}

export default MessageList
