import React, {useState} from "react";
import './style.css'

const LoginModal = (props) => {
    const [URL, setURL] = useState('https://chat.cs291.com')
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')

    function tryLogin(e) {
        e.preventDefault();
        let loginURL = URL + '/login'

        let data = new FormData();
        data.append('username', username);
        data.append('password', password);
        
        fetch(loginURL, {
            method: 'POST',
            body: data
        }).then(function(response) {
            if (response.ok) {
                return response.json();
            } else {
                throw new Error(response.status);
            }
        }).then(function(body) {
            sessionStorage.token = body['token'];
            sessionStorage.url = URL;

            props.onSetDisableChat(false);
        }).catch((status) => {
            console.log(status)
            if (status == 'Error: 403') {
                alert('Invalid username or password');
            }
            if (status == 'Error: 422') {
                alert(status + " failure to /login");
            }
        });
    };

    if (props.disableChat) {
        return (
            <div className="loginBody">
                <div className="loginContainer">
                    <h1 className='loginTitle'> DOGE Chat </h1> <br />
                    <form className='loginForm' onSubmit={tryLogin}>
                        <input placeholder='URL' className='loginInput' value={URL} onChange={(event) => setURL(event.target.value)} /><br/>
                        <input placeholder='username' className='loginInput' onChange={(event) => setUsername(event.target.value)} /><br/>
                        <input placeholder='password' type='password' className='loginInput' onChange={(event) => setPassword(event.target.value)} /><br/>
                    
                        <button className='submit' type="submit">Submit</button>
                    </form>
                </div>
            </div>
        )
    } else {
        return null
    }
}

export default LoginModal
