import React, {useState} from "react";
import './style.css'

const SendUserMessage = (props) => {
    const [message, setMessage] = useState('')
    
    function onSendMessage(e) {
        e.preventDefault();
        const messageURL = props.url + '/message'

        console.log(messageURL)
        
        let data = new FormData();
        data.append("message", message)

        fetch(messageURL, {
            method: 'POST',
            headers: new Headers({"Authorization": "Bearer " + sessionStorage.getItem('token')}),
            body: data
        }).then(function(response) {
            let status = response.status
            if (status === 422) {
                alert('Please send a nonempty message')
            }
            else if (status === 201) {
                setMessage('')
            }
            console.log("Token is: " + sessionStorage.getItem('token'))
            console.log(response);
        }).catch( err => {
            console.log("IN ERROR STATE")
            console.log(err);
        });

    };

    return (
        <form className="message_form" onSubmit={onSendMessage}>
            <input 
                className = "messageInput"
                placeholder="Type message here and press ENTER to send."
                type="text"
                value={message}
                onChange={(event) => {setMessage(event.target.value)}}
                disabled={props.disabled} />
        </form>
    )
}

export default SendUserMessage
